.popup__notification {
  font-size: 24px;
  line-height: 1.2;
  color: #000;
  text-align: center;
  width: calc(100% - 72px);
  margin: 32px auto 0 auto;
  font-weight: 900;
}

@media screen and (max-width: 425px) {
  .popup__notification {
    font-size: 20px;
    width: calc(100% - 36px);
  }
}